/* poppins-100 - latin */
@font-face {
	font-family: "Poppins";
	font-style: normal;
	font-weight: 100;
	src: url("../webfonts/poppins-v19-latin-100.eot"); /* IE9 Compat Modes */
	src: local(""),
		url("../webfonts/poppins-v19-latin-100.eot?#iefix")
			format("embedded-opentype"),
		/* IE6-IE8 */ url("../webfonts/poppins-v19-latin-100.woff2")
			format("woff2"),
		/* Super Modern Browsers */
			url("../webfonts/poppins-v19-latin-100.woff") format("woff"),
		/* Modern Browsers */ url("../webfonts/poppins-v19-latin-100.ttf")
			format("truetype"),
		/* Safari, Android, iOS */
			url("../webfonts/poppins-v19-latin-100.svg#Poppins") format("svg"); /* Legacy iOS */
}
